section.contact-page{
	height: 100vh;
	margin: 0 auto
}
section.contact-page .text-content{
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
@media (min-width: 1024px){
	section.contact-page .text-content{
		left: 40%;
		top: auto;
		bottom: 30%
	}
}
@media (min-width: 1280px){
	section.contact-page .text-content{
		bottom: auto;
		top: 50%
	}
}
section.contact-page .text-content p{
	font-size: 14px;
	line-height: 28px;
	letter-spacing: .5px;
	word-break: keep-all;
	color: #707070;
}
@media (min-width: 768px){
	section.contact-page .text-content p{
		font-size: 20px;
		line-height: 32px
	}
}
@media (min-width: 1280px){
	section.contact-page .text-content p{
		font-size: 24px;
		line-height: 36px
	}
}
section.contact-page .text-content p b{
	margin-left: 2px;
}
@media (min-width: 768px){
	section.contact-page .text-content p b{
		margin-left: 5px;
		border-bottom: 1px solid #707070
	}
}
@media (min-width: 768px){
	section.contact-page .text-content .email{
		margin-bottom: 20px
	}
}
@media (min-width: 1280px){
	section.contact-page .text-content .email{
		margin-bottom: 40px
	}
}
section.contact-page .deco{
	width: 100%;
	overflow: hidden;
	position: absolute;
	bottom: 80px;
	z-index: -1;
}
@media (min-width: 1024px){
	section.contact-page .deco{
		width: auto;
		height: 70vh;
		bottom: 0;
		left: 60%;
		-webkit-transform: translate(-50%, 0%);
		        transform: translate(-50%, 0%);
		bottom: 50px
	}
}
@media (min-width: 1540px){
	section.contact-page .deco{
		height: 50vh;
		bottom: 18%
	}
}
section.contact-page .deco img{
	width: 100%;
}
@media (min-width: 1024px){
	section.contact-page .deco img{
		width: auto;
		height: 100%
	}
}


