@media (min-width: 1024px){

    section.work-list{

        margin: 0 auto;
    }
}

section.work-list .mobile-list{

    padding-top: 80px;
}

@media (min-width: 768px){

    section.work-list .mobile-list{

        display: -webkit-box;

        display: -ms-flexbox;

        display: flex;

        -ms-flex-wrap: wrap;

            flex-wrap: wrap;

        padding-top: 120px;
    }
}

@media (min-width: 1024px){

    section.work-list .mobile-list{

        display: none;
    }
}

section.work-list .mobile-list .item-team{

    cursor: pointer;

    margin-bottom: 20px;
}

@media (min-width: 768px){

    section.work-list .mobile-list .item-team{

        width: calc(50% - 10px);

        margin-bottom: 40px;
    }
}

section.work-list .mobile-list .item-team .teamcover{

    width: 100%;
}

section.work-list .mobile-list .item-team .teamcover img{

    width: 100%;
}

section.work-list .mobile-list .item-team .teamname h1{

    font-size: 14px;

    letter-spacing: .5px;

    color: #323232;
}

@media (min-width: 768px){

    section.work-list .mobile-list .item-team .teamname h1{

        font-size: 20px;

        letter-spacing: 1px;
    }
}

@media (min-width: 768px){

    section.work-list .mobile-list .item-team:nth-child(odd){

        margin-right: 20px;
    }
}

section.work-list .desktop-list{

    display: none;
}

@media (min-width: 1024px){

    section.work-list .desktop-list{

        display: block;

        width: calc(100vw - 120px);

        height: 100vh;

        z-index: 10;

        overflow: hidden;

        -webkit-transition: .5s;

        transition: .5s;
    }
}

@media (min-width: 1280px){

    section.work-list .desktop-list{

        width: 100%;
    }
}

section.work-list .desktop-list .teams{

    padding-top: 150px;
}

section.work-list .desktop-list .teams .item-team{

    position: absolute;

    z-index: 10;
}

section.work-list .desktop-list .teams .item-team a{

    display: -moz-flex;

    display: -ms-flex;

    display: -o-flex;

    display: -webkit-box;

    display: -ms-flexbox;

    display: flex;

    -ms-align-items: center;

    -webkit-box-align: center;

        -ms-flex-align: center;

            align-items: center;
}

section.work-list .desktop-list .teams .item-team .eyeball{

    width: 3vw;

    max-width: 60px;
}

section.work-list .desktop-list .teams .item-team .eyeball img{

    width: 100%;
}

section.work-list .desktop-list .teams .item-team .teamname{

    font-size: 16px;

    margin-left: 16px;

    letter-spacing: .5px;

    color: #707070;

    opacity: 0;

    -webkit-transition: .5s;

    transition: .5s;
}

section.work-list .desktop-list .teams .item-team:first-child{

    left: 0%;

    top: 50%;
}

section.work-list .desktop-list .teams .item-team:nth-child(2){

    left: 5%;

    top: 32%;
}

section.work-list .desktop-list .teams .item-team:nth-child(3){

    left: 14%;

    top: 20%;
}

section.work-list .desktop-list .teams .item-team:nth-child(4){

    left: 13%;

    top: 75%;
}

section.work-list .desktop-list .teams .item-team:nth-child(5){

    left: 18%;

    top: 60%;
}

section.work-list .desktop-list .teams .item-team:nth-child(6){

    left: 24%;

    top: 42%;
}

section.work-list .desktop-list .teams .item-team:nth-child(7){

    left: 29%;

    top: 68%;
}

section.work-list .desktop-list .teams .item-team:nth-child(8){

    left: 34%;

    top: 85%;
}

section.work-list .desktop-list .teams .item-team:nth-child(9){

    left: 37%;

    top: 25%;
}

section.work-list .desktop-list .teams .item-team:nth-child(10){

    left: 44%;

    top: 72%;
}

section.work-list .desktop-list .teams .item-team:nth-child(11){

    left: 52%;

    top: 78%;
}

section.work-list .desktop-list .teams .item-team:nth-child(12){

    left: 60%;

    top: 35%;
}

section.work-list .desktop-list .teams .item-team:nth-child(13){

    left: 62%;

    top: 80%;
}

section.work-list .desktop-list .teams .item-team:nth-child(14){

    left: 65%;

    top: 19%;
}

section.work-list .desktop-list .teams .item-team:nth-child(15){

    left: 70%;

    top: 62%;
}

section.work-list .desktop-list .teams .item-team:nth-child(16){

    left: 73%;

    top: 47%;
}

section.work-list .desktop-list .teams .item-team:nth-child(17){

    left: 79%;

    top: 30%;
}

section.work-list .desktop-list .teams .item-team:nth-child(18){

    left: 82%;

    top: 73%;
}

section.work-list .desktop-list .teams .item-team:last-child{

    left: 92%;

    top: 65%;
}

section.work-list .desktop-list .teams .item-team:last-child .teamname{

    width: 60px;
}

section.work-list .desktop-list .teamcover{

    height: 60vh;

    width: 60vh;

    border-radius: 50%;

    background-size: cover;

    background-position: 50% center;

    position: absolute;

    top: 50%;

    left: 50%;

    -webkit-transform: translate(-50%, -50%);

            transform: translate(-50%, -50%);

    z-index: -1;

    -webkit-transition: .3s;

    transition: .3s;
}

section.work-list .desktop-list .teamcover img{

    width: 100%;

    -webkit-transform: translateX(5%);

            transform: translateX(5%);
}

section.work-list .desktop-list .bg-maincover{

    border-radius: 50%;

    width: 400px;

    height: 400px;

    background-size: cover;

    background-position: center;

    position: absolute;

    left: 50%;

    top: 50%;

    -webkit-transform: translate(-50%, -50%);

            transform: translate(-50%, -50%);

    z-index: -1;
}

section.work-list .desktop-list .teams .item-team .eyeball:hover + .teamname{

    opacity: 1;

    -webkit-transition: .5s;

    transition: .5s;
}

